.body {
    background: linear-gradient(rgb(216, 16, 149), rgb(255, 230, 0));
    height: 100vh;
    flex: 0.6;
    color: rgb(109, 109, 109);
    padding: 30px;
    overflow-y: scroll;
    position: fixed;
    margin-left: 23vw;
    width: 77vw;
}
.body::-webkit-scrollbar {
    display: none;
}
.body__info {
    display: flex;
    align-items: flex-end;
    padding: 10px;
}
.body__info > img {
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}
.body__infoText {
    flex: 1;
}
.body__infoText > h2 {
    font-size: 48px;
    margin-bottom: 10px;
    color: white;
}
.body__infoText > p {
    font-size: 14px;
}
.body__shuffle {
    font-size: 80px !important;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.body__shuffle:hover {
    transition: 100ms transform ease-in;
    transform: scale(1.08);
}
.body__icons {
    display: flex;
    align-items: center;
}
.body__icons > .MuiSvgIcon-root {
    margin-right: 20px;
}
.body__songs {
    margin: 20px -30px;
}