.sidebarOption {
    display: flex;
    align-items: center;
    color: white;
    height: 40px;
    cursor: pointer;
    transition: 200ms border ease-in;
}

.sidebarOption:hover {
    border-bottom: gray solid 1px;
    border-top: gray solid 1px;
    width: 90%;
    margin: 10px auto;
}

.sidebar>hr {
    border: gray 1px solid;
    width: 90%;
    margin: 10px auto;
}

.sidebarOption_icon {
    padding-left: 10px;
    padding-right: 10px;
}

.sidebarOption>p {
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}