.sidebar {
    background: linear-gradient(rgb(216, 16, 149), rgba(243, 55, 227, 0.836), rgb(216, 16, 149));
    box-shadow: 5px 0px 50px ;
    height: 100vh;
    flex: 0.4;
    z-index: 1;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0px;
    position: fixed;
    width: 23vw;
}

.acme {
    font-size: 3vw;
    font-family: 'Acme';
    color: white;
}

.sofia {
    font-size: 3vw;
    padding-left: 4px;
    font-family: 'sofia';
    color: white;
}

.title {
    text-align: center;
}

.sidebar_title {
    margin-left: 10px;
    padding: 5px;
    font-size: 12px;
}

strong {
    color: white;
}
