.footer {
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    height: 65px;
    width: 100%;
    background: linear-gradient(90deg, rgb(255, 245, 102), rgb(255, 230, 0), rgb(255, 230, 0), rgb(255, 245, 102));
    box-shadow: 0px 0px 20px;
    z-index: 2;
}

.footer_left {
    padding: 5px;
    flex: 0.3;
    color: white;
    display: flex;
    align-items: center;
    width: 300px;
}
.footer__songInfo>h4 {
    margin-bottom: 5px;
}

.footer__songInfo>p {
    font-size: 12px;
}
.footer__green {
    color: #1ed15e;
}

.footer__icon:hover,
.footer__green:hover {
    transition: transform 0.2s ease-in-out;
    transform: scale(1.2) !important;
}

.footer__albumLogo {
    height: 60px;
    width: 60px;
    margin-right: 20px;
    object-fit: contain;
}
.footer_center {
    flex: 0.4;
    padding: 0 100px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer__right {
    flex: 0.3;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer__right * .MuiSlider-root {
    color: green;
}
