.row__title {
    color: white;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 1px solid white;
}
.row {
    margin-bottom: 20px;
}
.rox__body {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}
.row__songs {
    object-fit: contain;
    padding: 15px;
    margin-left: 50px;
    margin-right: 50px;
    display: flex;
    flex-direction: row;
    min-height: 100px;
    max-height: 200px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.song > img {
    height: 180px;
    width: 150px;
    /* object-fit: contain; */
    margin: 0px 10px;
    cursor: pointer;
    transition: transform 450ms;
}
.song > img:hover {
    transform: scale(1.08);
}
*::-webkit-scrollbar {
    display: none;
}
.song__name {
    text-align: center;
    font-size: 15px;
    color: rgb(99, 99, 99);
}
.song__info {
    text-align: center;
    font-size: 10px;
    color: rgb(85, 84, 84);
}